/** @jsx jsx */
import { jsx, css } from '@emotion/core';
import { graphql } from 'gatsby';
import { Box } from '@material-ui/core';
import BioCard from 'components/Cards/BioCard';
import Layout from 'components/Layout';
import SEO from 'components/SEO';
import Posts from 'components/Posts';
import RecentPostsCard from 'components/Cards/RecentPostsCard';
import CategoriesCard from 'src/components/Cards/CategoriesCard';
import Paginator from 'src/components/Paginator';
import { Fragment } from 'react';

interface Props {
  data: {
    allMarkdownRemark: any;
    site: {
      siteMetadata: {
        title: string;
      };
    };
  };
  location: Location;
}

const PostsTemplate = ({ data, location }: Props) => {
  const {
    allMarkdownRemark: { edges: posts, pageInfo },
  } = data;

  return (
    <Layout
      location={location}
      left={
        <Fragment>
          <Box
            css={css`
              margin-bottom: 1rem;
            `}
          >
            <BioCard />
          </Box>
          <Box>
            <CategoriesCard />
          </Box>
        </Fragment>
      }
      right={
        <Box>
          <RecentPostsCard />
        </Box>
      }
    >
      <SEO />
      <Posts posts={posts.map((p: { node: any }) => p.node)} />
      <Paginator pageCount={pageInfo.pageCount} currentPage={pageInfo.currentPage} defaultUrl="/" />
    </Layout>
  );
};

export default PostsTemplate;

export const pageQuery = graphql`
  query PostsQuery($skip: Int!, $limit: Int!) {
    allMarkdownRemark(skip: $skip, limit: $limit, sort: { fields: [frontmatter___date], order: DESC }) {
      edges {
        node {
          excerpt(pruneLength: 300)
          fields {
            slug
          }
          frontmatter {
            date(formatString: "YYYY-MM-DD")
            title
            path
            categories
            thumbnail {
              childImageSharp {
                fluid(maxWidth: 800) {
                  ...GatsbyImageSharpFluid
                }
              }
            }
          }
        }
      }
      pageInfo {
        currentPage
        pageCount
      }
    }
  }
`;
